-deets {\n  font-size: 0.6em;\n}\n\n.player-score-deets .fas {\n  color: lightgray;\n  margin-right: 5px;\n}\n\n.fa-crown.first-place {\n  color: gold;\n}\n</style>"],"sourceRoot":""}]);
