___CSS_LOADER_EXPORT___.push([module.id, "\n.chat-message[data-v-37135cbc] {\n    text-align: left;\n    padding: 5px 10px;\n}\n.chat-notification[data-v-37135cbc] {\n  text-align: left;\n  padding: 1px 10px;\n}\n.chat-author[data-v-37135cbc] {\n  font-size: 0.8em;\n  color: gray;\n      margin-right: 15px;\n    font-style: italic;\n}\n.notif[data-v-37135cbc] {\n  font-size: 0.8em;\n  font-style: italic;\n  color: gray;\n}\n", "",{"version":3,"sources":["webpack://./front/components/chatMessage.vue"],"names":[],"mappings":";AAsDA;IACI,gBAAgB;IAChB,iBAAiB;AACrB;AAEA;EACE,gBAAgB;EAChB,iBAAiB;AACnB;AAEA;EACE,gBAAgB;EAChB,WAAW;MACP,kBAAkB;IACpB,kBAAkB;AACtB;AAEA;EACE,gBAAgB;EAChB,kBAAkB;EAClB,WAAW;AACb","sourcesContent":["<template>\n  <div class=\"chat-message\" v-if=\"isMessage\">\n    <span class=\"chat-author\">{{ author }}</span><br/>\n    <div class=\"chat-message-content\">\n      <div class=\"chat-message-content\" v-for=\"msg in message.messages\">{{ msg }}</div>\n    </div>\n  </div>\n  <div class=\"chat-notification\" v-else-if=\"isNotif\">\n    <span class=\"notif\">{{ notification }}</span>\n  </div>\n</template>\n\n<script>\nimport constants from \"../common/constants\";\n\nexport default {\nname: \"chatMessage\",\n  props: {\n    message: Object,\n  },\n  computed: {\n    author() {\n      return this.getPlayerName(this.message.author);\n    },\n\n    notification() {\n      if (!this.isNotif) { return \"\"}\n      const content = this.message.content;\n      switch (content.type) {\n        case constants.chatItemTypes.NOTIF_LAUNCH:\n          return `${this.getPlayerName(content.from)} a lancé la partie`;\n        case constants.chatItemTypes.NOTIF_RELAUNCH:\n          return `${this.getPlayerName(content.from)} a relancé la partie`;\n        case constants.chatItemTypes.NOTIF_WINNER:\n          return `${this.getPlayerName(content.player)} a remporté la partie !`;\n        case constants.chatItemTypes.NOTIF_NEW_SCORE:\n          return `${this.getPlayerName(content.player)} a marqué ${content.score} point` + (content.score > 1 ? \"s\" : \"\");\n        case constants.chatItemTypes.NOTIF_NEW_HINT:\n          return `À trouver : ${content.hint}`;\n      }\n    },\n\n    isMessage () {\n      return this.message.type === constants.chatItemTypes.MESSAGE;\n    },\n\n    isNotif () {\n      return this.message.type === constants.chatItemTypes.NOTIFICATION;\n    }\n  }\n}\n</script>\n\n<style scoped>\n.chat-message {\n    text-align: left;\n    padding: 5px 10px;\n}\n\n.chat-notification {\n  text-align: left;\n  padding: 1px 10px;\n}\n\n.chat-author {\n  font-size: 0.8em;\n  color: gray;\n      margin-right: 15px;\n    font-style: italic;\n}\n\n.notif {\n  font-size: 0.8em;\n  font-style: italic;\n  color: gray;\n}\n</style>"],"sourceRoot":""}]);
